import { Icon, TextField, Tooltip } from '@vaadin/react-components';
import React, { Component } from 'react';

interface CustomTextFieldsProps {
  renderTextField: number;
}

class CustomTextFields extends Component<CustomTextFieldsProps> {
  renderTextField(fieldNumber: number) {
    switch (fieldNumber) {
      case 1:
        return (
          <TextField label="Model location"
                     helperText="add custom LLM location(not recomended)"
                     placeholder="default: /home/USER/project-ai-initializer/models"
                     clearButtonVisible>
            <Tooltip slot="tooltip" text="Tooltip text 1" />
            <Icon slot="prefix" icon="vaadin:vaadin-h" />
            <span slot="suffix">:)</span>
          </TextField>
        );
      case 2:
        return (
          <TextField label="Snapshots location"
                     helperText="add custom LLM snapshot location(not recomended)"
                     placeholder="default: /home/USER/project-ai-initializer/models/.snapshots"
                     clearButtonVisible>
            <Tooltip slot="tooltip" text="Tooltip text 2" />
            <Icon slot="prefix" icon="vaadin:vaadin-h" />
            <span slot="suffix">:)</span>
          </TextField>
        );
      case 3:
        return (
          <TextField label="path" helperText="enter path to directory"
                     placeholder="/path/to/directory" clearButtonVisible>
            <Tooltip slot="tooltip" text="Tooltip text 3" />
            <Icon slot="prefix" icon="vaadin:vaadin-h" />
            <span slot="suffix">:)</span>
          </TextField>
        );
      case 4:
        return (
          <TextField label="file path" helperText="enter path to file"
                     placeholder="/path/to/file.*" clearButtonVisible>
            <Tooltip slot="tooltip" text="Tooltip text 4" />
            <Icon slot="prefix" icon="vaadin:vaadin-h" />
            <span slot="suffix">:)</span>
          </TextField>
        );
      case 5:
        return (
          <TextField label="File 5" helperText="Helper text 5"
                     placeholder="Placeholder 5" clearButtonVisible>
            <Tooltip slot="tooltip" text="Tooltip text 5" />
            <Icon slot="prefix" icon="vaadin:vaadin-h" />
            <span slot="suffix">:)</span>
          </TextField>
        );
      case 6:
        return (
          <TextField label="File 6" helperText="Helper text 6"
                     placeholder="Placeholder 6" clearButtonVisible>
            <Tooltip slot="tooltip" text="Tooltip text 6" />
            <Icon slot="prefix" icon="vaadin:vaadin-h" />
            <span slot="suffix">:)</span>
          </TextField>
        );
      default:
        return null;
    }
  }

  render() {
    const { renderTextField } = this.props;
    return (
      <div>
        {this.renderTextField(renderTextField)}
      </div>
    );
  }
}

export default CustomTextFields;
